/* 스마트폰과 가로 방향 */
@media only screen and (min-width: 320px) and (max-width: 767px) {
  /* 스타일 작성 */
  .chartBox {
    display: none;
  }
  .clock {
    display: none;
  }
}

/* 데스크탑 및 가로 방향 */
@media only screen and (min-width: 768px) {
  /* 스타일 작성 */
  .wrap {
    padding: 40px 5% 40px 7%;
  }
  .container {
    width: 100%;
    border-radius: 55px;
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
    overflow: hidden;
    padding: 60px 5%;
  }
  .themeBtn {
    display: flex;
    position: absolute;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    top: 95px;
    left: calc(7% - 44.3px);
  }

  .choiceChip {
    height: 32px;
    border-radius: 10px 0 0 10px;
    margin: 4px;
  }
  .greeting {
    display: flex;
    align-items: flex-start;
  }
  .title {
    font-size: 24px;
    font-weight: bold;
    color: #141212;
    margin-bottom: 24px;
  }

  .timeBox {
    border: 5px solid;
    display: flex;
    align-items: center;
    justify-content: space-around;
    height: 240px;
    padding: 24px 20px;
    border-radius: 15px;
    margin-right: 24px;
    width: 100%;
  }

  .dailyInfo {
    display: flex;
    margin-bottom: 14px;
  }

  .topBtn {
    position: fixed;
    bottom: 50px;
    left: 2%;
    width: 30px;
    height: 40px;
    border-radius: 15px;
    padding: 12px 7px;
    z-index: 100;
  }

  .clock {
    display: none;
  }

  .chartBox {
    display: none;
  }

  /* weatherComponent */
  .weatherImageDiv {
    border-radius: 15px;
    height: 240px;
    background-position: center;
    background-size: cover;
    backdrop-filter: blur(5px);
    overflow: hidden;
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    padding: 24px 20px;
  }
  .weatherDiv {
    position: relative;
    margin-right: 24px;
    min-width: 170px;
    max-width: 200px;
    width: 100%;
  }

  .geoInfo {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .cityName {
    font-size: 22px;
    margin-top: 12px;
  }
  .todayFormat {
    font-size: 12px;
    font-weight: lighter;
  }
  .tempBox {
    font-size: 38px;
    display: flex;
    flex-direction: column;
    font-weight: bold;
    align-items: center;
  }
  .tempLine {
    width: 65px;
    height: 1px;
    border: 0.3px solid #141212;
  }
  .weatherInfo {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .weatherIcon {
    height: 60px;
  }

  /* CalendarComponent */
  .calendarDiv {
    width: 350px;
  }
  .month {
    display: flex;
    justify-content: space-around;
    margin: 2px 0 7px 0;
  }
  #calendar {
    width: 350px;
    border-collapse: collapse;
  }
  #calendar tr,
  #calendar tbody {
    grid-column: 1 / -1;
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    width: 350px;
    border-collapse: collapse;
  }
  #calendar td,
  #calendar th {
    padding: 3px 4px;
    box-sizing: border-box;
    border-collapse: collapse;
    border: 0.1px solid #eee;
  }
  #calendar td:first-child {
    border-left: none;
  }
  #calendar td:last-child {
    border-right: none;
  }
  #calendar tr:last-child {
    border: none;
  }
  #calendar .weekdays th {
    text-align: center;
    text-transform: uppercase;
    border: none !important;
    padding: 4px 6px;
    color: #fff;
    font-size: 13px;
  }

  #calendar td {
    max-height: 40px;
    display: flex;
    /* flex-direction: column; */
  }

  #calendar .date {
    position: relative;
    margin-bottom: 5px;
    padding: 2px;
    width: 20px;
    font-size: small;
    font-weight: bold;
  }

  #calendar .event {
    flex: 0 0 auto;
    font-size: 12px;
    border-radius: 4px;
    padding: 3px 5px;
    text-align: center;
    line-height: 16.5px;
    background: #e4f2f2;
    border: 1px solid #b5dbdc;
    color: #009aaf;
    text-decoration: none;
    margin-left: 3px;
  }

  td.selected-date {
    box-shadow: inset 0px 0px 10px #2196f3;
    border-radius: 5px;
  }
  td.current-date {
    box-shadow: inset 0px 0px 10px #009aaf;
    border-radius: 5px;
  }

  /* Notice */
  .noticeDiv {
    margin: 10px 0 15px 0;
    box-sizing: border-box;
  }
  .noticeDiv h2 {
    font-size: 20px;
    font-weight: bold;
    letter-spacing: 1px;
    margin-bottom: 5px;
  }
  .noticeList {
    border-radius: 15px;
    min-height: 90px;
    box-shadow: 0px 0px 5px #444;
    background-color: #ffffff7a;
    border: 5px solid;
    padding: 24px 20px;
  }
  .noticeList table {
    width: 100%;
  }
  .noticeList tr,
  .noticeList tbody {
    grid-column: 1 / -1;
    display: grid;
    grid-template-columns: 2fr 1fr 1fr 1fr;
    border-collapse: collapse;
  }
  .noticeList td,
  .noticeList th {
    border-collapse: collapse;
    text-align: center;
  }
  .noticeList td:first-child,
  .noticeList th:first-child {
    text-align: left;
    padding-left: 10px;
  }
  .noticeList tr:not(:last-child) {
    margin-bottom: 5px;
  }
  .noticeList th {
    font-size: 16px;
    font-weight: bold;
    text-transform: uppercase;
    color: #666;
    padding-bottom: 5px;
    margin-bottom: 5px;
    border-bottom: 2px solid #666;
  }
  .noticeList td {
    padding: 3px 0;
  }
  .noticeList > table > tbody > tr > td:last-child {
    border-radius: 13px;
    padding: 3px 0;
    margin: 0 24px;
    color: #fff;
  }
  .noticeList > table > tbody > tr > td:nth-child(3) {
    border: 1px solid;
    border-radius: 13px;
    padding: 3px 0;
    margin: 0 24px;
  }
  .noticeList .clearMsg {
    display: flex;
    align-items: center;
  }
  .noticeList p {
    line-height: 32px;
    font-style: oblique;
    color: #666;
  }

  /* TodoInput */
  .inputDiv {
    margin: 15px 0 28px 0;
  }
  .inputTitle {
    display: flex;
    align-items: center;
    margin-bottom: 5px;
  }
  .inputTitle h2 {
    font-size: 20px;
    font-weight: bold;
    letter-spacing: 1px;
    margin-top: 5px;
  }
  .inputs {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    background-color: #6666662a;
    border: 5px solid;
    padding: 24px 20px;
    border-radius: 15px;
  }
  .text {
    width: 40%;
  }
  .date {
    width: 50px;
  }
  .memo {
    width: 40%;
  }
  .text,
  .inputs .date,
  .category,
  .memo {
    font-size: 15px;
    line-height: 18px;
    border: 0;
    border-radius: 15px;
    outline: none;
    padding: 8px 15px;
    background-color: #fff;
    margin-right: 14px;
  }
  .inputs > button {
    width: 41px;
    height: 41px;
    font-size: 25px;
    border-radius: 15px;
    padding: 8px;
    color: #fff;
  }
  input[type="date"]::-webkit-datetime-edit-text {
    -webkit-appearance: none;
    display: none;
  }

  input[type="date"]::-webkit-datetime-edit-month-field {
    -webkit-appearance: none;
    display: none;
  }

  input[type="date"]::-webkit-datetime-edit-day-field {
    -webkit-appearance: none;
    display: none;
  }

  input[type="date"]::-webkit-datetime-edit-year-field {
    -webkit-appearance: none;
    display: none;
  }

  /* todoList */
  .listDiv {
    position: relative;
    margin-top: 55px;
  }
  .filteringBtn {
    position: absolute;
    top: -29px;
    left: 15px;
  }
  .filteringBtn button,
  .deleteAll {
    border-top: 5px solid;
    border-left: 5px solid;
    border-right: 5px solid;
    border-radius: 15px 15px 0 0;
    padding: 5px 12px 0 12px;
  }
  .filteringBtn button:not(:nth-child(1)) {
    margin-left: -2.5px;
  }
  .selected-category {
    background-color: #6666662a;
    z-index: 1;
    box-shadow: inset 0px 0px 10px #666;
  }
  .none {
    background-color: #ffffff7a;
  }
  .deleteAll {
    position: absolute;
    top: -29px;
    background-color: #ffffff7a;
    right: 15px;
  }
  .todoList {
    box-shadow: 0px 0px 5px #444;
    border: 5px solid;
    padding: 24px 20px;
    border-radius: 15px;
    background-color: #ffffff7a;
  }
  .todoLi {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: 5px 0;
    width: 100%;
  }
  .memoBtn,
  .editBtn,
  .deleteBtn,
  .importantBtn,
  .completeBtn {
    width: 50px;
    line-height: 18px;
    font-size: 20px;
    padding-top: 3px;
  }
  .todoDue {
    width: 100px;
  }
  .todoTask {
    width: calc(100% - 350px);
  }
  .editBox {
    background-color: #6666662a;
    border-radius: 0 0 8px 8px;
    display: flex;
    align-items: baseline;
    justify-content: flex-end;
    padding: 12px 15px;
  }
  .editTask,
  .editCategory,
  .editMemo,
  .editDate {
    font-size: 15px;
    line-height: 18px;
    border: 0;
    border-radius: 15px;
    outline: none;
    padding: 8px 15px;
    background-color: #fff;
    margin-right: 14px;
  }
  .editTask {
    width: calc(100% - 490px);
  }
  .editDate {
    width: 50px;
  }
  .editBtnBox > button {
    color: #fff;
    padding: 4px 0;
    border-radius: 8px;
    width: 80px;
  }
  .editSave {
    margin-bottom: 5px;
  }
  .editBtnBox {
    display: flex;
    flex-direction: column;
    width: 80px;
    align-items: center;
  }
}

/* 태블릿 및 가로 방향 */
@media only screen and (min-width: 1024px) {
  .timeBox {
    min-width: 564px;
  }
  /* clockComponent*/
  .clock {
    display: block;
    width: 140px;
    height: 140px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: url("../../public/clock.png");
    background-size: cover;
    border: 2px solid #141212;
    box-shadow: inset 0px 0px 10px #666;
    border-radius: 50%;
    margin-right: 24px;
  }

  .clock:before {
    content: "";
    position: absolute;
    width: 7px;
    height: 7px;
    background: #fff;
    border-radius: 50%;
    z-index: 10;
  }

  .clock .hour,
  .clock .min,
  .clock .sec {
    position: absolute;
  }

  .clock .hour .hr {
    width: 70px;
    height: 70px;
  }

  .clock .min .mn {
    width: 90px;
    height: 90px;
  }

  .clock .sec .sc {
    width: 100px;
    height: 100px;
  }

  .hr,
  .mn,
  .sc {
    display: flex;
    justify-content: center;
    border-radius: 50%;
  }

  .hr:before {
    content: "";
    position: absolute;
    width: 6px;
    height: 35px;
    background: #3f3f3f;
    z-index: 10;
    border-radius: 6px 6px 0 0;
  }

  .mn:before {
    content: "";
    position: absolute;
    width: 3px;
    height: 45px;
    background: #666;
    z-index: 10;
    border-radius: 6px 6px 0 0;
  }

  .sc:before {
    content: "";
    position: absolute;
    width: 2px;
    height: 50px;
    background: #ff105e;
    z-index: 10;
    border-radius: 6px 6px 0 0;
  }

  /* dougthnutChart */
  .chartBox {
    /* mainpage */
    border: 5px solid;
    height: 240px;
    padding: 3px;
    border-radius: 15px;
    min-width: 35px;
    display: block;
  }
  .doughnutChart {
    display: none;
  }
  .thinChart {
    display: block;
    width: 100%;
    height: 100%;
    border-radius: 10px;
    overflow: hidden;
    position: relative;
  }
  .progressBar {
    width: 100%;
    position: absolute;
    bottom: 0;
    border-radius: 8px 8px 0 0;
    transition: all 1s;
  }
  .progressBar span {
    writing-mode: vertical-lr;
    color: #fff;
    font-size: 10px;
    margin-top: 10px;
    line-height: 18px;
  }
  .chartDiv {
    height: 100%;
  }
}

/* 데스크탑 대형 화면 및 가로 방향 */
@media only screen and (min-width: 1200px) {
  /* 스타일 작성 */
  .doughnutChart {
    display: block;
    width: 100%;
    padding: 5px 5px 10px 5px;
    position: relative;
  }
  .doughnutChart span {
    font-size: 18px;
    margin-left: calc(50% - 18px);
    font-weight: bold;
  }
  .thinChart {
    display: none;
  }
  .chartBox {
    min-width: 150px;
    width: 30%;
  }
}
